<template><section><h1>PlaneGeometry、 PlaneOutlineGeometry</h1>
<ul>
<li><code v-pre>vc-geometry-plane</code> 组件用于加载平面几何体。</li>
<li><code v-pre>vc-geometry-outline-plane</code> 组件用于加载平面几何体轮廓。</li>
<li>需要作为 <code v-pre>vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre>vc-primitive</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载平面几何体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes" :modelMatrix="modelMatrix">
            <vc-geometry-plane ref="plane" :vertexFormat="vertexFormat"></vc-geometry-plane>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearanceOutline">
          <vc-instance-geometry :attributes="attributesOutline" :modelMatrix="modelMatrix">
            <vc-geometry-outline-plane ref="planeOutline"></vc-geometry-outline-plane>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
