          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-outline-plane</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"planeOutline"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-outline-plane</span>&gt;</span>
