        <span class="hljs-attr">attributes</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">vertexFormat</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">modelMatrix</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">attributesOutline</span>: <span class="hljs-literal">null</span>
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.plane.createPromise, <span class="hljs-keyword">this</span>.$refs.planeOutline.createPromise]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'All geometries are loaded.'</span>)
        <span class="hljs-keyword">const</span> boundingSphereUnion = instances.reduce(<span class="hljs-function">(<span class="hljs-params">prev, cur</span>) =&gt;</span> {
          <span class="hljs-keyword">const</span> geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
